import VeLine from 'v-charts/lib/line'

export default {
  name: '',
  mixins: [],
  props: {
  },
  data () {
    return {
      chartData: null,
      chartSettings: {
        label: {
          normal: { show: true }
        }
      }
    }
  },
  computed: {},
  components: {
    VeLine
  },
  methods: {},
  created () {
    this.$nextTick(() => {
      setTimeout(() => {
        this.chartData = {
          columns: ['', '-1'],
          rows: [
            { '': '11', '-1': 1523, '-2': 1523, '': 0.12, '': 100 },
            { '': '12', '-1': 1223, '-2': 1523, '': 0.345, '': 100 },
            { '': '13', '-1': 2123, '-2': 1523, '': 0.7, '': 100 },
            { '': '14', '-1': 4123, '-2': 1523, '': 0.31, '': 100 },
            { '': '15', '-1': 3123, '-2': 1523, '': 0.12, '': 100 },
            { '': '16', '-1': 7123, '-2': 1523, '': 0.65, '': 100 }
          ]
        }
      }, 3000)
    })
  },
  mounted () {}
}



